import React, { useState } from "react";
import { render } from "react-dom";

import Dropdown from "react-dropdown";
import "react-dropdown/style.css";

const brandoptions = ["iPhone", "Samsung", "OPPO"];
const modelsoptions = ["12 Pro Max", "12 Pro", "12", "12 Mini"];
const storageoptions = ["16gb", "32gb", "64gb", "128gb", "256gb", "512mb"];

const RepairService = () => {
  const [showModel, setShowModel] = useState(false);
  const [showStorage, setShowStorage] = useState(false);

  const brandSelect = () => {
    console.log("Brand Selected");
    setShowModel(true);
  };

  const modelSelect = () => {
    console.log("Model Selected");
    setShowStorage(true);
  };

  const storageSelect = () => {
    console.log("Model Selected");
  };

  return (
    <div className="container mobile-service">
      <div className="row ">
        <div className="col-md-12">
          <label>Brands</label>
          <Dropdown
            options={brandoptions}
            onChange={brandSelect}
            placeholder="Select an option"
          />
        </div>
        <div className={`col-md-12 ${showModel ? " " : "d-none"} `}>
          <label>Models</label>
          <Dropdown
            options={modelsoptions}
            onChange={modelSelect}
            placeholder="Select an option"
          />
        </div>
        <div className={`col-md-12 ${showStorage ? " " : "d-none"} `}>
          <label>Storage</label>
          <Dropdown
            options={storageoptions}
            onChange={storageSelect}
            placeholder="Select an option"
          />
        </div>
      </div>
    </div>
  );
};

export default RepairService;
